<!-- components/UserForm.vue -->
<template>
  <q-dialog v-model="dialogVisible">
    <div class="q-dialog__inner">
      <q-card style="min-width: 400px;">
        <q-card-section>
          <div class="text-h6 q-mb-sm">เพิ่มผู้ใช้งาน</div>
          <q-form @submit.prevent="submitUser" @reset="onReset">
            <q-input v-model="user.username" label="Username *" lazy-rules
              :rules="[val => val && val.length > 0 || 'Username is required!']" />
            <q-input v-model="user.password" label="password *" lazy-rules
              :rules="[val => val && val.length > 0 || 'password is required!']" />
            <q-input v-model="confirmpassword" label="Confirm Password *" lazy-rules
              :rules="[val => val === user.password || 'Password and Confirm password not matched!']" />
            <q-input v-model="user.fname" label="fname *" lazy-rules
              :rules="[val => val && val.length > 0 || 'fname is required!']" />
            <q-input v-model="user.lname" label="lname *" lazy-rules
              :rules="[val => val && val.length > 0 || 'lname is required!']" />
            <div>
              <q-btn label="Submit" type="submit" color="green" />
              <q-btn label="Reset" type="reset" color="primary" flat class="q-ml-sm" />
            </div>
          </q-form>
        </q-card-section>
      </q-card>
    </div>
  </q-dialog>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      user: {
        username: '',
        fname: '',
        lname: '',
        password: '',
      },
      confirmpassword: '',
      token: this.$store.state.token
    };
  },
  methods: {
    openDialog() {
      this.dialogVisible = true;
    },
    closeDialog() {
      this.dialogVisible = false;
    },
    submitUser() {
      try {
        this.$axios.post('/api/user/register', {
          username: this.user.username,
          password: this.user.password,
          fname: this.user.fname,
          lname: this.user.lname,
        }, {
          headers: {
            'authorization': `Bearer ${this.token}`
          }
        }).then(async response => {
          if (response.data.status === 401 || response.data.status === "false") {
            await this.$q.notify({
              message: response.data.error.message,
              color: 'red'
            })
          } else {
            await this.$q.notify({
              message: response.data.message,
              color: 'green'
            })
            this.$emit('formSubmitted', true);
          }
        })
      } catch (error) {
        console.log(error)
      }
      this.closeDialog();
    },
    onReset() {
      this.user.username = '';
      this.user.fname = '';
      this.user.lname = '';
      this.user.password = '';
      this.confirmpassword = '';
    }
  },
};
</script>