<template>
    <div class="q-pa-xl">
        <div class="row q-mb-sm">
            <div class="col-12">
                <q-input v-model="search" label="Search by ( Username, First Name, Last Name )" outlined dense />
            </div>
        </div>
        <div class="row">
            <div class="col">
                <q-btn size="sm" label=" delete
            selected" color="purple" class="q-my-md" @click="onDeleteMultiples">
                </q-btn>
            </div>
        </div>

        <q-table table-header-style="background-color: green;color:white" title="ผู้ใช้งานทั้งหมด"
            rows-per-page-label="Rows per page" :rows-per-page="rowsPerPage" :rows="filteredData" :columns="columns"
            :filter="search" virtual-scroll row-key="username" primary class="text-center" selection="multiple"
            v-model:selected="selected">
            <template v-slot:top-right>
                <q-file color="teal" @input="fileChange" v-model="file" filled label="Upload File" class="q-mr-sm">
                    <template v-slot:prepend>
                        <q-icon name="cloud_upload" />
                    </template>
                </q-file>
                <q-btn size="md" label="ตัวอย่างไฟล์" color="teal" class="q-my-md q-mr-sm" href="/Example.xlsx">
                </q-btn>
                <q-btn size="md" icon="add" label="เพิ่มผู้ใช้งาน" color="green" @click="openUserForm" class="q-my-md">
                </q-btn>
            </template>
            <template v-slot:body-cell-avatar="props">
                <q-td :props="props">
                    <q-avatar size="80px">
                        <q-img :src="`${getImg()}`" />
                    </q-avatar>
                </q-td>
            </template>
            <template v-slot:body-cell-auth_status="props">
                <q-td @click="changeAuthStatus(props.row.username, props.row.auth_status)">
                    <q-badge v-if="props.row.auth_status == '1'" outline color="secondary" label="ใช้งาน" />
                    <q-badge v-else outline color="red" label="ไม่ได้ใช้งาน" />
                </q-td>
            </template>
            <template v-slot:body-cell-status_lock="props">
                <q-td @click="changeStatusLock(props.row.username, props.row.status_lock)">
                    <q-badge v-if="props.row.status_lock == '1'" outline color="orange" label="ถูกล็อค" />
                    <q-badge v-else outline color="secondary" label="ปกติ" />
                </q-td>
            </template>
            <template v-slot:body-cell-actions="props">
                <q-td :props="props">
                    <q-icon name="refresh" cursor="pointer" size="2rem" color="brown 5"
                        @click="onResetPassword(props.row.username)" v-if="props.row.user_role === 'user'">
                    </q-icon>
                    <q-icon size="2rem" name="delete" color="deep-orange q-ml-sm" @click="onDelete(props.row.username)"
                        v-if="props.row.user_role === 'user'">
                    </q-icon>
                </q-td>
            </template>

            <template v-slot:body-cell-created_date="props">
                <q-td>
                    {{ formatDate(props.row.created_date) }}
                </q-td>
            </template>

            <template v-slot:body-cell-user_role="props">
                <q-td>
                    <q-badge v-if="props.row.user_role == 'admin'" outline class="q-ml-sm" color="orange" label="Admin" />
                    <q-badge v-else outline color="secondary" label="User" />
                </q-td>
            </template>
        </q-table>
    </div>
    <UserForm ref="userForm" @formSubmitted="registeredUser()" />
</template>

<script>
import moment from 'moment'
import UserForm from '@/components/AddUserForm.vue';
import readXlsxFile from 'read-excel-file'

export default {
    components: {
        UserForm,
        readXlsxFile
    },
    data() {
        return {
            users: [],
            token: this.$store.state.token, // token users for request api
            // Set header title of table
            columns: [
                { name: 'avatar', align: 'center', field: 'avatar', sortable: true },
                { name: 'auth_status', align: 'center', field: 'auth_status', label: 'สถานะการใช้งาน', sortable: true },
                { name: 'status_lock', align: 'center', field: 'status_lock', label: 'Lock', sortable: true },
                { name: 'username', align: 'center', label: 'Username', field: 'username', sortable: true },
                { name: 'fname', align: 'center', label: 'First Name', field: 'fname', sortable: true },
                { name: 'lname', align: 'center', label: 'Last Name', field: 'lname', sortable: true },
                { name: 'user_role', align: 'center', label: 'ระดับสิทธิ์', field: 'user_role', sortable: true },
                { name: 'created_date', align: 'center', label: 'Created At', field: 'created_date', sortable: true },
                { name: 'actions', align: 'center', label: 'Actions', field: 'actions', sortable: true },
            ],
            search: '', // Serach input text box for search data of table 
            rowsPerPage: 5,
            selected: [],
            selectedDelete: [],
            showAddUserForm: false,
            dense: false,
            file: null
        }
    },
    created() {
        if (!this.$store.state.user) return this.$router.push('/login');
        this.getUsersList();
    },
    computed: {
        filteredData() {
            if (this.search) {
                const lowerSearch = this.search.toLowerCase();
                return this.users.filter((item) => {
                    return (item.username && item.username.toLowerCase().includes(lowerSearch)) ||
                        (item.fname && item.fname.toLowerCase().includes(lowerSearch)) ||
                        (item.lname && item.lname.toLowerCase().includes(lowerSearch)) ||
                        (item.user_role && item.user_role.toLowerCase().includes(lowerSearch))
                });
            } else {
                return this.users;
            }
        },
    },
    methods: {
        // reset password
        onResetPassword(username) {
            this.$q.dialog({
                title: 'รีเซ็ต Password',
                message: 'โปรดใส่ Password ใหม่',
                prompt: {
                    model: '',
                    type: 'text' // optional
                }
            }).onOk(newpassword => {
                try {
                    this.$axios.put('/api/user/passwordchange/' + username, { newpassword: newpassword }, {
                        headers: {
                            'authorization': `Bearer ${this.token}`
                        }
                    }).then(async response => {
                        if (response.data.status === 401 || response.data.status === "false") {
                            console.log(response)
                            await this.$q.notify({
                                message: response.data.error[0].msg,
                                color: 'red'
                            })
                        } else {
                            await this.$q.notify({
                                message: response.data.message,
                                color: 'green'
                            })
                            await this.getUsersList()
                        }
                    })
                } catch (error) {
                    console.log(error)
                }
            })
        },
        // delete user 1 user
        onDelete(username) {
            this.$q.dialog({
                title: 'Confirm Delete',
                message: `คุณต้องการลบข้อมูล Username : ${username} ?`,
                ok: 'Yes',
                cancel: 'No',
            }).onOk(() => {
                try {
                    this.$axios.delete('/api/user/' + username, {
                        headers: {
                            'authorization': `Bearer ${this.token}`
                        }
                    }).then(async response => {
                        if (response.data.status === 401 || response.data.status === "false") {
                            await this.$q.notify({
                                message: response.data.error.message,
                                color: 'red'
                            })
                        } else {
                            await this.$q.notify({
                                message: response.data.message,
                                color: 'green'
                            })
                            await this.getUsersList()
                        }
                    })
                } catch (error) {
                    console.log(error)
                }
            });
        },
        // get avatar img
        getImg() {
            let lastChar = Math.floor(Math.random() * 5) + 1;
            return 'https://cdn.quasar.dev/img/avatar' + lastChar + '.jpg'
        },
        //fetch user list
        getUsersList() {
            try {
                this.$axios.get('/api/user', {
                    headers: {
                        'authorization': `Bearer ${this.token}`
                    }
                }).then(async response => {
                    if (response.data.status === 401 || response.data.status === "false") {
                        await alert('คุณไม่มีสิทธิ์เข้าถึงหน้านี้');
                        await localStorage.removeItem("token");
                        await localStorage.removeItem("user");
                        this.$router.push('/')
                    } else {
                        await this.$store.dispatch('setUsersList', response.data.data);
                        this.users = this.$store.state.usersList
                        this.$router.push('/users')
                    }
                })
            } catch (error) {
                console.log(error)
            }
        },
        //format datetime to YYYY-MM-DD H:i:s
        formatDate(datetime) {
            return moment(datetime).format('DD MMMM YYYY HH:MM')
        },
        //update auth status
        changeAuthStatus(username, status) {
            let newStatus = (status == '1') ? 0 : 1;
            let msg = (newStatus) ? 'ใช้งาน' : 'ไม่ใช้งาน';
            this.$q.dialog({
                title: 'Confirm Change Status!',
                message: `คุณต้องการปรับสถานะการใช้งาน Username เป็น '${msg}'' ?`,
                ok: 'Yes',
                cancel: 'No',
            }).onOk(() => {
                try {
                    this.$axios.put('/api/user/authstatus/' + username, { newStatus: newStatus }, {
                        headers: {
                            'authorization': `Bearer ${this.token}`
                        }
                    }).then(async response => {
                        if (response.data.status === 401 || response.data.status === "false") {
                            await this.$q.notify({
                                message: response.data.error.message,
                                color: 'red'
                            })
                        } else {
                            await this.$q.notify({
                                message: response.data.message,
                                color: 'green'
                            })
                            await this.getUsersList()
                        }
                    })
                } catch (error) {
                    console.log(error)
                }
            });
        },
        //update lock status
        changeStatusLock(username, status) {
            let newStatus = (status == '1') ? 0 : 1;
            let msg = (newStatus) ? 'ถูกล็อค' : 'ปกติ';
            this.$q.dialog({
                title: 'Confirm Change Status!',
                message: `คุณต้องการปรับสถานะการใช้งาน Username เป็น '${msg}'' ?`,
                ok: 'Yes',
                cancel: 'No',
            }).onOk(() => {
                try {
                    this.$axios.put('/api/user/statuslock/' + username, { newStatus: newStatus }, {
                        headers: {
                            'authorization': `Bearer ${this.token}`
                        }
                    }).then(async response => {
                        if (response.data.status === 401 || response.data.status === "false") {
                            await this.$q.notify({
                                message: response.data.error.message,
                                color: 'red'
                            })
                        } else {
                            await this.$q.notify({
                                message: response.data.message,
                                color: 'green'
                            })
                            await this.getUsersList()
                        }
                    })
                } catch (error) {
                    console.log(error)
                }
            });
        },

        // delete multiple user
        async onDeleteMultiples() {
            this.selectedDelete = await this.selected.map(user => user.username)
            if (this.selectedDelete.length === 0) {
                this.$q.dialog({
                    title: 'ข้อมูลว่าง',
                    message: `โปรดเลือกข้อมูลที่ต้องการลบ`,
                    ok: 'ok',
                }).onOk(() => { })
            } else {
                this.$q.dialog({
                    title: 'Confirm Delete',
                    message: `คุณต้องการลบข้อมูล Username  ${this.selectedDelete}  ?`,
                    ok: 'Yes',
                    cancel: 'No',
                }).onOk(() => {
                    try {
                        this.$axios.post('/api/user/deleteMultiples', {
                            usersList: this.selectedDelete
                        }, {
                            headers: {
                                'authorization': `Bearer ${this.token}`
                            }
                        }).then(async response => {
                            if (response.data.status === 401 || response.data.status === "false") {
                                await this.$q.notify({
                                    message: response.data.error.message,
                                    color: 'red'
                                })
                            } else {
                                await this.$q.notify({
                                    message: response.data.message,
                                    color: 'green'
                                })
                                await this.getUsersList()
                            }
                        })
                    } catch (error) {
                        console.log(error)
                    }
                });
            }
        },
        openUserForm() {
            this.$refs.userForm.openDialog();
        },
        registeredUser() {
            this.getUsersList();
        },
        fileChange(event) {
            // const users = []
            let xlsxfile = event.target.files ? event.target.files[0] : null;
            readXlsxFile(xlsxfile).then((rows) => {
                // console.log(rows)
                const users = []
                rows.map((user, index) => {
                    if (index !== 0) {
                        users.push({
                            username: user[1],
                            password: user[2],
                            fname: user[3],
                            lname: user[4]
                        })
                    }
                })
                if (users.length > 0) {
                    this.$axios.post('/api/user/registerMultiple', { users: users }, {
                        headers: {
                            'authorization': `Bearer ${this.token}`
                        }
                    }).then(async (res) => {
                        let messageSuccess = '<div class = "text-green text-h6">ผู้ใช้งานที่เพิ่มสำเร็จ</div>';
                        let messageFalse = '<div class = "text-red text-h6">ผู้ใช้งานที่เพิ่มไม่สำเร็จ</div>';
                        await res.data.results.map((user, index) => {
                            return (res.data.results[index].status === "ok")
                                ? messageSuccess += `<p class = "text-green q-pl-md q-pt-sm">${user.message}</p>`
                                : messageFalse += `<p class = 'text-red q-pl-md q-pt-sm'>${user.message}</p>`
                        })
                        await this.$q.dialog({
                            title: 'รายงาน<em>!</em>',
                            message: messageSuccess + "<br>" + messageFalse,
                            html: true
                        }).onOk(async () => {
                            this.file = null
                            await this.getUsersList()
                        })
                    }).catch(err => {
                        console.log(err)
                    })
                }
                // console.log(users)
            }).catch(err => {
                console.log('Please upload only type .xlsx file!')
            })
        },
        downloadFile() {
            const assetUrl = '/Example.xlsx'
            const link = document.createElement('a');
            link.href = assetUrl;
            link.download = fileName;
            link.click();

        }
    }
}
</script>
<style lang="css" scoped>
.btn,
.q-btn {
    transition: transform 0.3s;
}

.btn:hover,
.q-btn:hover {
    transform: scale(110%);
}

.q-img,
.q-icon {
    transition: transform 0.3s;
}

.q-img:hover,
.q-icon:hover {
    transform: scale(110%);
}

.q-icon:hover,
.q-badge:hover {
    cursor: pointer;
}

.q-tr-selected {
    background-color: #e2f2ff;
    /* สีพื้นหลังของแถวที่เลือก */
}
</style>